.education-container {
    width: 100%;
    overflow-x: hidden;
  }
  .education-top-section {
    height: 600px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .education-font48 {
    font-size: 48px;
    text-align: center;
  }
  
  /* .fellowship-page {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    padding: 0px 123px;
  } */
  
  .edu-second-div {
    display: flex;
    justify-content: space-between;
    gap: 15px;
    padding: 50px 0px;
  }
  
  .edu-sub-div {
    display: flex;
    flex-direction: column;
    /* width: 465px; */
    text-align: left;
  }
  .edu-fellow-img {
    height: 298px;
    /* width: 465px; */
  }
  .edu-second-div > .edu-sub-div > .edu-impact-title {
    padding: 12px 0px;
    font-size: 36px;
  }
  
  .edu-second-div > .edu-sub-div > .edu-impact-data {
    font-size: 28px;
  }
  .edu-third-div {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: 150px auto;
  }
  .edu-impact-head {
    font-size: 24px;
    padding: 40px 50px;
  }
  .edu-third-div > .edu-impact-title {
    padding: 0px 150px 80px;
    font-size: 36px;
  }
  .edu-marquee-cont {
    height: 360px;
    overflow-y: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .edu-mar-img {
    width: 200px;
    height: 200px;
    border-radius: 5px;
    margin: 30px;
  }
  .button {
    cursor: pointer;
    outline: none;
    background-color: white;
    border: none;
    align-self: flex-end;
  }
  .edu-fellowship-sec {
    width: 860px;
    height: 280px;
    margin: 150px auto;
  }
  
  .edu-font40 {
    font-size: 40px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 20px;
  }
  .edu-font28 {
    font-size: 28px;
    text-align: center;
  }
  .edu-last-sec {
    height: 640px;
    width: 100%;
    background-color: #d9d9d9;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 100px;
  }
  .edu-font24 {
    font-size: 24px;
    margin-bottom: 20px;
  }
  .edu-font48-normal {
    font-size: 48px;
    font-weight: 400;
    width: 840px;
    text-align: center;
    line-height: 48px;
    margin-bottom: 20px;
  }
  
  .edu-apply-btn {
    height: 40px;
    width: 150px;
    background-color: white;
    border-radius: 30px;
    border: none;
    cursor: pointer;
    outline: none;
  }
  @media screen and (max-width: 393px) {
    .education-top-container {
      height: 400px;
    }
    .education-font48 {
      font-size: 40px;
    }
    .fellowship-page {
      padding: 0;
      width: calc(100% - 32px);
      margin: auto;
    }
    .edu-second-div {
      flex-direction: column;
      height: auto;
    }
    .edu-sub-div {
      margin: 50px auto;
    }
    .edu-second-div > .edu-sub-div > .edu-impact-title {
      font-size: 32px;
    }
    .edu-second-div > .edu-sub-div > .edu-impact-data {
      font-size: 16px;
    }
    .edu-third-div {
      width: calc(100% - 32px);
      margin: auto;
    }
    .edu-impact-head {
      font-size: 16px;
    }
    .edu-third-div > .edu-impact-title {
      font-size: 28px;
      padding: 0;
    }
    .edu-marquee-cont {
      height: 200px;
      margin: 100px auto;
    }
    .edu-mar-img {
      height: 120px;
      width: 120px;
      margin: 20px;
    }
    .edu-fellowship-sec {
      width: calc(100% - 32px);
      margin: auto;
      height: auto;
    }
    .edu-font28 {
      font-size: 16px;
      margin-bottom: 30px;
    }
    .edu-last-sec {
      height: 450px;
      margin-top: 100px;
    }
    .edu-font24 {
      font-size: 20px;
      font-weight: bold;
      margin-top: 50px;
    }
    .edu-font48-normal {
      font-size: 28px;
      width: calc(100% - 60px);
      margin: auto;
      line-height: 35px;
      text-align: center;
    }
    .edu-apply-btn {
      margin-bottom: 50px;
    }
  }
  